export let group_allowed_graphemes = {}
let a_v = []
let a_c = []
// let a_b = []
for (const [group, _] of Object.entries(word_groups)) {
  if(group.includes("Add")){
    let new_graphemes = group.split("_").slice(1)
    a_v = [...a_v]
    a_c = [...a_c]
    for (let grapheme of new_graphemes) {
      if(isVowel(grapheme)){
        a_v.push(grapheme) 
      }else{
        a_c.push(grapheme) 
      }
    }
  }else if(group.includes("Blending")){
    let new_graphemes = group.split("_").slice(1)
    // a_b = []
    // for (let bk of blend_kinds){
    //   let okay = true
    //   for(let letter of bk.split('')){
    //     if(!a_c.includes(letter.toUpperCase())){
    //       okay = false
    //     }
    //   }
    //   if(okay){a_b.push(bk)}
    // }
  }
  // group_allowed_graphemes[group] = {"vowels": a_v, "consonants" : a_c, "blends" : a_b}
  group_allowed_graphemes[group] = {"vowels": a_v, "consonants" : a_c}
}


console.log("group_allowed_graphemes:", group_allowed_graphemes)

