// import { getAnalytics } from "firebase/analytics";
import { login_symbol_images } from '../assets/login_symbols';
import AnimatedEllipsis from '../util/AnimatedEllipsis';


function timeout(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
}
const AnimatedTouchable = Animated.createAnimatedComponent(TouchableOpacity);

async function getUserData(db, password) {
  const q = query(collection(db, "users"), where("password", "==", password));
  const querySnapshot = await getDocs(q);
  const results = querySnapshot.docs//.map(doc => doc.data())
  if(results.length >= 1){
    if(results.length > 1){console.warn("Multiple users with same password!")}

    let out = [results[0].ref, results[0].data()]
    console.log("GOT_USER:", out[0].id, querySnapshot, results, out[0], out[1])
    return out
  }else{
    return [null,null]
  }
}

async function initializeUser(db, password, condition_data={}) {
  console.log(db)
  let usersCollectionRef = collection(db, "users")
  console.log(usersCollectionRef)
  let data = {
    password : password,
    date_created: new Date().toUTCString(),
    transactions : [],
    stars : {words: {}, total_gold: 0, total_silver: 0},
    condition : condition_data
  }
  let newUserRef = await addDoc(usersCollectionRef, data)
  console.log("NEW_USER:", newUserRef,data)

  return [newUserRef, data]
}


const password_symbolset = ["CIRCLE","CAR","PAW","LEAF","SQUARE","AIRPLANE","WATER" ,"BUG"]
const password_length = 4

const colors = ['#eb1d1d','#e3cd1a','#5ecd3b', '#4383db']

var random_password = () => {
  var pw = []
  for(var i=0; i<password_length; i++){
    var sym = password_symbolset[Math.floor(Math.random() * password_symbolset.length)];
    pw.push(sym)
  }
  return pw.join(",")
}

async function findUnusedPassword(db){
  let password
  while(true) {
    password = random_password()
    let [user,data] = await getUserData(db, password)
    if(user == null){
      break
    }
  }
  return password.split(",")
}


const default_anim_config = {
    // bounciness : 0.01,
    speed: 20,
    bounciness : 80,
    // strength: 0.05,
    useNativeDriver: false,
    overshootClamping: true,
}

const CONDITION_OPTIONS = [
  {"id" : 1, "feedback_condition" : "copy", "sound_out" : true},
  {"id" : 2, "feedback_condition" : "whole_word", "sound_out" : true},
  {"id" : 3, "feedback_condition" : "incremental", "sound_out" : true},
  {"id" : 4, "feedback_condition" : "copy", "sound_out" : false},
  {"id" : 5, "feedback_condition" : "whole_word", "sound_out" : false},
  {"id" : 6, "feedback_condition" : "incremental", "sound_out" : false},
]


export default class LoginPage extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      "placed_symbols" : [null,null,null,null],
      "mode" : "waiting_input",
      condition_selection_index : 0,
    }
    this.symbol_opacities = this.state.placed_symbols.map(() => new Animated.Value(0))
    this.keyboard_scale_anim = new Animated.Value(1)
    this.new_user_button_pressed = false
    // this.done_button_scale_anim = new Animated.Value(1)
    // this.done_button_y_anim = new Animated.Value(0)
    // this.done_button_rotate_anim = new Animated.Value(0)
    this.prompt_opacity_anim = new Animated.Value(0)
    this.prompt_offsetY_anim = new Animated.Value(0)



  }

  

  addSymbol = (x,i=null) => {
    if(i==null){i = this.state.placed_symbols.indexOf(null)}
    if(i == -1){
      // this.setState({"mode": "displaying_password"})
      return
    }
    let new_placed_symbols = [...this.state.placed_symbols]
    new_placed_symbols[i] = x
    this.setState({"placed_symbols":new_placed_symbols})
    console.log(new_placed_symbols)
    this.symbol_opacities[i].setValue(0)

    let promise = new Promise( async (resolve, reject) => {
      Animated.spring(this.symbol_opacities[i],
        {...default_anim_config, toValue : 1}).start(() =>{resolve()})
    })
    return promise

  }

  eraseSymbol = (i = null) => {
    // let i = -1
    // console.log("TTT", i)
    for(let j=this.state.placed_symbols.length-1; j >= 0; j--){
      if(this.state.placed_symbols[j] != null){i = j; break;}
    }
    let new_placed_symbols = [...this.state.placed_symbols]
    new_placed_symbols[i] = null
    this.setState({"placed_symbols":new_placed_symbols})
    // Animated.spring(this.symbol_opacities[i] , {...default_anim_config, toValue : 1}).start()    
  }

  eraseAllSymbols = () =>{
    this.setState({"placed_symbols":this.state.placed_symbols.map(()=>null),
                  "mode" : "waiting_input",
                  "prompt_text" : ""})
    Animated.spring(this.keyboard_scale_anim,
      {...default_anim_config, speed: 50, toValue : 1}
    ).start()
  }

  // submitPassword = async () => {
  //   let password = this.state.placed_symbols.join(",")
  //   let user_data = await getUserData(this.props.firestore, password)
  //   console.log("user_data:",  user_data)

  //   if(user_data != null){
  //     this.props.login_callback(user_data)
  //   }
  // }

  render_keyboard_row = (start,end) => {
    let row = []
    for(let i=start; i <= end; i++){
      let item = password_symbolset[i];
      row.push(
        <TouchableOpacity 
          style={styles.symbol_button}
          onPress={()=>{this.addSymbol(item)}}
          key={"sym_button"+i}
        > 
          <Image 
            style={{  height: "90%", width: '90%', aspectRatio: 1, tintColor:'#222'}}
            resizeMode='contain'
            source={login_symbol_images[item.toLowerCase()]}                
          /> 
        </TouchableOpacity>    
      )
    }
    // console.log(row)
    return (<View style={styles.symbol_keyboard_row}>{row}</View>)
  }

  render_condition_buttons = (n_per_row) => {

    let n_rows = Math.ceil(CONDITION_OPTIONS.length/n_per_row)
    let n_per_col = Math.floor(CONDITION_OPTIONS.length/n_rows)
    let c = 0
    let rows = []
    for(let row_ind=0; row_ind < n_rows; row_ind++){

      let row = []
      for(let i=0; i < n_per_col; i++){
        let cond = CONDITION_OPTIONS[c]
        let is_sel = (this.state.condition_selection_index == c)
        let ind = c
        row.push(
          <TouchableOpacity style={[styles.condition_button, is_sel && {"backgroundColor" : "#9e9"}]}
            onPress={()=>{this.setState({condition_selection_index : ind})}} key={cond['id']}> 
            <Text style={{textAlign:"center", fontWeight: 'bold', "fontSize" : 13}}>
            {"(" + cond['id'] + ")\n" + " Feedback_Condition :\n" + cond["feedback_condition"] + "\n\n" +
                   "Sound_Out :\n" + cond["sound_out"]}
            </Text>
          </TouchableOpacity>
        )
        c++
      }
      rows.push(
        <View style={[styles.condition_button_row]} key={row_ind}> 
          {row}      
        </View>
      )
    }
    return (<View>{rows}</View>)

  }

  pressNewUserButton = () => {
    console.log("PRESS")
    this.new_user_button_pressed = true
    let prev = this.lastNewUserButtonPress = Date.now()
    Animated.spring(this.keyboard_scale_anim,
      {...default_anim_config, speed: 2, toValue : 0}
    ).start(()=>{
      console.log("NEW USER", this.lastNewUserButtonPress, Date.now())
      if(this.lastNewUserButtonPress == prev){
        this.newUser()  
      }
      
    })    
  }

  releaseNewUserButton = () => {
    if(this.state.mode != "waiting_input"){return}
    this.lastNewUserButtonPress = -1
    this.keyboard_scale_anim.stopAnimation()
    
    Animated.spring(this.keyboard_scale_anim,
      {...default_anim_config, speed: 50, toValue : 1}
    ).start()
     // console.log("RELEASE")
  }

  makePromptPopupAnim = () => {
    this.prompt_opacity_anim.setValue(0)
    this.prompt_offsetY_anim.setValue(50)
    let anims = [
      Animated.spring(this.prompt_opacity_anim,
        {...default_anim_config, speed: 5, toValue : 1}
      ),
      Animated.spring(this.prompt_offsetY_anim,
        {...default_anim_config, speed: 5, toValue : 0}
      )
    ]
    return Animated.parallel(anims)
  }

  newUser = async () => {
    this.setState({mode: "finding_unused_password"})
    let password = await findUnusedPassword(this.props.firestore)
    console.log("PASSWORD", password)
    this.setState({mode: "displaying_new_password",
                  "placed_symbols":this.state.placed_symbols.map(()=>null),
                  "prompt_text": "Your Password",
                  "prompt_text_color": "black",
                  "prompt_text_size": 30,
    })
    let popup_anim = this.makePromptPopupAnim()
    popup_anim.start()  
    await timeout(200)
    for(let i=0; i < password.length; i++){
      if(this.state.mode == "displaying_new_password"){
        await this.addSymbol(password[i],i)  
      }
    }
  }

  validatePassword = async () =>{
    this.setState({mode: "validating_password"})
    let [user,data] = await getUserData(this.props.firestore, this.state.placed_symbols.join(","))
    this.finalizeUser(user,data)
  }

  initializeProfile = async () => {
    this.setState({mode: "initializing_new_user", "prompt_text" : ""})
    console.log("INIT PROFILE START", this.state.condition_selection_index) 
    let condition_data = CONDITION_OPTIONS[this.state.condition_selection_index]
    let [user,data] = await initializeUser(this.props.firestore, this.state.placed_symbols.join(","), condition_data)
    this.finalizeUser(user,data)
  }

  finalizeUser = async (user,data) =>{
    if(user != null){
      this.setState({mode: "user_confirmed",
                     prompt_text : "",
                     prompt_text_color: "green",
                     prompt_text_size: 80,
      })  
      
    }else{
      this.setState({mode: "waiting_input", 
                     prompt_text : "",
                     prompt_text_color: "red",
                     prompt_text_size: 80,
      })  
    }

    let popup_anim = this.makePromptPopupAnim()
    if(user != null){
      popup_anim.start(async () => {
        await timeout(500)
        this.props.login_callback(user,data)  
      })  
    }else{
      popup_anim.start()  
    }
  }

  
  render = () =>{
    let mode = this.state.mode
    let is_waiting_network = (mode == "finding_unused_password" || mode == "initializing_new_user")
    let can_press_done = !is_waiting_network && this.state.placed_symbols.indexOf(null) == -1
    let can_press_erase = !is_waiting_network && this.state.placed_symbols.some((x) => x != null)
    console.log("MODE", mode)

    let done_button_func = (mode == "displaying_new_password" && this.initializeProfile) || this.validatePassword 
    let displaying_password = mode == "displaying_new_password"

    return (
      <View style={styles.container}>
        {mode=="waiting_input" && 
          <TouchableOpacity 
            style={styles.new_user_button}
            onPressIn={()=>this.pressNewUserButton()}
            onPressOut={()=>this.releaseNewUserButton()}
          >        
          <Text selectable={false} style={{fontSize: 20}}>New User</Text>
          </TouchableOpacity>
        }

        <View style={styles.prompt_container}>
          {is_waiting_network && 
            <AnimatedEllipsis numberOfDots={3} unselectable="on" style={{color:'black', fontSize:72, margin:5}}/>
          }
          {!!this.state.prompt_text && 
           <Animated.Text style={{fontSize:this.state.prompt_text_size,
                                  opacity:this.prompt_opacity_anim,
                                  transform : [{"translateY" : this.prompt_offsetY_anim}],
                                  color : this.state.prompt_text_color,
                                  // color : this.state.prompt_text_color,
            }}>{this.state.prompt_text}</Animated.Text> 
          }
        </View>

        <View style={styles.password_line}>
          {this.state.placed_symbols.map((item,i) => 
           <View key={"sym"+i} style={[styles.password_item,{borderColor:colors[i]}]}> 
            {item &&
              <Animated.Image 
                  style={{  height: "90%", width: '90%', aspectRatio: 1,
                   tintColor:colors[i], opacity: this.symbol_opacities[i]}}
                  resizeMode='contain'
                  source={login_symbol_images[item.toLowerCase()]}

              /> 
            }
           </View> 
          )}
        </View>
        {(!displaying_password &&
          <Animated.View style={[styles.symbol_keyboard,{'transform' : [{scaleY: this.keyboard_scale_anim}] }]}> 
            {this.render_keyboard_row(0,3)}
            {this.render_keyboard_row(4,7)}
          </Animated.View>) ||
          this.render_condition_buttons(3)
        }
        <View style={styles.control_button_container}> 
          <TouchableOpacity onPress={(can_press_done && done_button_func) || undefined}
            style={[styles.control_button, styles.done_button, 
                    !can_press_done && styles.control_botton_inactive, 
            // {transform: [
            //   {"scale" : this.done_button_scale_anim},
            //   {"translateY" : this.done_button_y_anim},
            //   {"rotate" : this.done_button_rotate_anim.interpolate(
            //       {inputRange: [0,360],  outputRange: ['0deg', '360deg']})
            //   },
            // ]}
          ]}> 

            <Text style={{fontSize:80}}>{""}</Text>
          </TouchableOpacity>

          <TouchableOpacity onPress={(can_press_erase && (() => this.eraseAllSymbols())) || undefined}
            style={[styles.control_button, styles.erase_button, 
                    !can_press_erase && styles.control_botton_inactive, 
            // {transform: [
            //   {"scale" : this.done_button_scale_anim},
            //   {"translateY" : this.done_button_y_anim},
            //   {"rotate" : this.done_button_rotate_anim.interpolate(
            //       {inputRange: [0,360],  outputRange: ['0deg', '360deg']})
            //   },
            // ]}
          ]}> 
            <Text style={{fontSize:80}}>{""}</Text>
          </TouchableOpacity>

        </View>
      </View>
    )
  }
}


