let SOUND_OUT_CONFIG = {
  "sound_out_spelling" : true,
  "sound_out_correction" : true,
}
let NO_SOUND_OUT_CONFIG = {
  "sound_out_spelling" : false,
  "sound_out_correction" : false,
}

const sound_out_configs = {
  true : SOUND_OUT_CONFIG,
  false : NO_SOUND_OUT_CONFIG
}

let COPY_CONFIG = {
  "show_correct_at_prompt" : true,
  "verify_correct" : true,
  "verify_phonetically_correct" : false,
  "incorrect_feedback_type" : "none",
}

let WHOLE_WORD_CONFIG = {
  "show_correct_at_prompt" : false,
  "verify_correct" : true,
  "verify_phonetically_correct" : false,
  "incorrect_feedback_type" : "whole_word",
}

let INCREMENTAL_CONFIG = {
  "show_correct_at_prompt" : false,
  "verify_correct" : true,
  "verify_phonetically_correct" : true,
  "incorrect_feedback_type" : "incremental",
}

const feedback_condition_configs = {
  copy : COPY_CONFIG,
  whole_word : WHOLE_WORD_CONFIG,
  incremental : INCREMENTAL_CONFIG,
}



