export default class App extends React.Component {
  constructor(props){
    super(props)
    let user_data = DEBUG_STUDENT && debug_students_data[DEBUG_STUDENT] || undefined;
    let kt = (user_data && user_data['knowledge_trace']) || {}
    let {word, unlocked_words, word_group_ind} = this.nextWordState(kt)

    this.state = {
      // problem_sequence : [...problem_sequence],
      repetition : 1,
      user_data : user_data, 
      user_ref : undefined,
      word : word,
      unlocked_words : unlocked_words,
      word_group_ind : word_group_ind,
      word_history : [],
    }
    window.goToWord = (word) =>{
      if(!all_words.includes(word.toLowerCase())){
        alert("Word: " + word + " is not included in app.")
      }else{
        this.setState({
          repetition : 1,
          word : word
        })  
      }
    }
  }

  nextWordState = (kt) => {
    let state = this.state || {}
    kt = kt || (state.user_data || {'knowledge_trace' : {}})['knowledge_trace']
    let unlocked_words = state.unlocked_words

    // let unlocked_words;
    let word_group_ind = state.word_group_ind || 0
    // word_group_ind = (word_group_ind || this.state.word_group_ind) || 0
    let new_group_ind = check_next_word_group_ind(kt, word_group_ind)
    if(!unlocked_words || new_group_ind != word_group_ind){
      word_group_ind = new_group_ind
      unlocked_words = [].concat(...Object.values(word_groups).slice(0,new_group_ind+1))
      // this.setState("word" : word, "unlocked_words" :  "ind_of_max_unlocked_group" : new_group_ind)
    }
    let word = next_word(unlocked_words, kt, state.word_history)

    console.log("WORD", word, word_group_ind, unlocked_words)
    return {word, unlocked_words, word_group_ind}
  }

  userDidLogin = (user_ref,user_data) => {
    console.log("LOGIN", user_ref, user_data)
    console.log(Timestamp.now())
    // updateDoc(user_ref, "transactions", arrayUnion({"timestamp" : Timestamp.now(),"HI": "word" : "", "spelling" : "", "correction_seq": ""}))
    // let userRef = user.ref
    // console.log(userRef)
    // let transactions = ref(userRef,'transactions')
    // console.log(transactions)
    // let newTransaction = push(transactions)
    // console.log(newTransaction)
    // set(newTransaction, {"HI": 7})
    let ws = this.nextWordState()
    this.setState({"user_ref": user_ref, user_data: user_data, ...ws}) 
    
  }

  endSession = () => {
    console.log("END SESSION")
    this.setState({
      user_data : undefined, 
      // problem_number : 0,
      repetition : 1
    })
  }

  onProblemDone = (kt, stars, force_next=false)=>{
    console.log(stars)
    console.log(kt)
    // console.log(force_next, this.state.repetition, NUM_REPS)
    let user_data = {...this.state.user_data, stars : stars, knowledge_trace: kt}
    var next_state;
    console.log(next_state)
    if(!force_next && this.state.repetition < NUM_REPS){
      next_state = {
        user_data : user_data,
        repetition: this.state.repetition+1
      }
      console.log("PROBLEM DONE", {...kt}, next_state)
      this.setState(next_state)
    }else{
      // console.log(next_state)
      // next_state.repetition = 1
      let word_states = this.nextWordState(kt)
      let word_history = [...(this.state.word_history || []), word_states.word],
      next_state = {
        user_data : user_data,
        repetition: 1,
        ...word_states,
        word_history : word_history
      }
      console.log("PROBLEM DONE", {...kt}, next_state)
      this.setState(next_state)
    }
    
    
  }

  

  


  render = () => {
    // let word = this.state.problem_sequence[this.state.problem_number]
    let word = this.state.word
    console.log("RENDER", word)

    let user_data = this.state.user_data || {condition : {feedback_condition : "incremental", sound_out : true}}
    console.log("user_data", this.state.user_data, user_data)
    let user_condition = user_data.condition || {}
    console.log("user_condition", user_condition)
    let feedback_mode_props =  feedback_condition_configs[user_condition.feedback_condition || "incremental"] || INCREMENTAL_CONFIG
    let should_sound_out = user_condition.sound_out
    if(should_sound_out == undefined){should_sound_out = true}
    let sound_out_props =  sound_out_configs[should_sound_out] || SOUND_OUT_CONFIG
    let condition_props = {...feedback_mode_props, ...sound_out_props}//CONDITION_PROPS[user_data.condition || "incremental"] || INCREMENTAL_CONDITION
    console.log("condition_props", condition_props)
    // console.log(word)
    return (
      <View style={styles.container}>
        {(this.state.user_data || SKIP_LOGIN) && <ISProblem
          onDone={this.onProblemDone} 
          onEndSession={this.endSession}
          word={word}
          word_info={words_info[word] || {}}
          repetition={this.state.repetition}
          grapheme_options = {grapheme_options[word]}
          user_ref={this.state.user_ref}
          user_data={this.state.user_data}
          {...condition_props} 
          // {...INCREMENTAL_CONDITION } 
          // firestore={firestore}
        /> || 
        <LoginPage firestore={firestore} login_callback={this.userDidLogin}/>}
        {/*<Test style={{height: "100%"}}/>*/}
        
      </View>
    );
  }
}

