export let default_params = {"init": .2, "transit" : .1, "slip": .2, "guess" : .1}

export let bkt_update = (p_k, o, params=default_params) => {
	let [t, s, g] = [params['transit'],params['slip'],params['guess']]
	// console.log(t, s, g)
	let p_k_o;
	if(o == 'correct' || o > 0){
		let p_correct = (p_k * (1-s) + (1-p_k) * g)
		p_k_o = (p_k * (1-s)) / p_correct
	}else{
		let p_incorrect = (p_k * s     + (1-p_k) * (1-g))
		p_k_o = (p_k * s)  / p_incorrect
	}
	// console.log(p_k_o)

	let new_p_k = p_k_o + (1-p_k_o)*t
	return new_p_k
}


export let find_knowlege_trace_observations = (word, spelling, corr_seq, use_word_kcs=false) =>{
	let word_info = words_info[word.toLowerCase()]
  let abs_correct = word.toUpperCase() == spelling.toUpperCase()
  // console.log(word.toUpperCase(), spelling.toUpperCase(), abs_correct)
  if(!corr_seq){
    corr_seq = best_correction_sequence(word,spelling,false)
  }

  let user_data = {"knowldge_trace" : {}}
  // let word = "CAT"
  let bbd = best_break_down(word, word)
  // console.log(bbd)
  let graphemes = []
  for (var [ps,gs] of bbd){
    if(["CH", "TH", "SH"].includes(ps[0]) ){
      graphemes = graphemes.concat([gs.join("")])
    }else{
      graphemes = graphemes.concat(gs)
    }
  }
      // return graphemes
  // console.log(graphemes)
  // let graphemes = ISProblem.get_word_graphemes("CAT")
  let obs = {}
  for(let gr of graphemes){obs[gr] = 1}
  for(let bl of word_info.blends){obs[bl.toUpperCase()+"_blend"] = 1}
  // let spelling = "CLG"
  
  if(!Array.isArray(spelling)){spelling = spelling.split("")}
  // console.log(corr_seq)
  for(let corr of corr_seq){
    obs[corr.insert_value] = 0

    if(corr.insert_index > spelling.length){
      console.log("error")
    }
    if(corr.insert_value){
      spelling.splice(corr.insert_index,corr.remove_length, corr.insert_value)
    }else{
      spelling.splice(corr.insert_index,corr.remove_length)
    }
    let jlice = (a,b) => {return spelling.slice(a,b).join("").toLowerCase()}
    // console.log("<<", spelling)
    let sl = spelling.join("").toLowerCase()
    let ci = corr.insert_index
    let blends = word_info.blends || []

    // console.log(corr.insert_value, "<<", spelling)
    // console.log(blends, ci)
    // let [ia,ic,id] = [ib-2,ib-1, ib+1, ib+2]
    let [b,c] = [jlice(ci-1,ci+1),jlice(ci,ci+2)]
    let [ib,ic] = [blends.indexOf(b),blends.indexOf(c)]
    // console.log(b,c, ib,ic)
    // if(ia >= 0){obs[a.toUpperCase()+"_blend"] = 0}
    if(ib >= 0){obs[b.toUpperCase()+"_blend"] = 0}
    if(ic >= 0){obs[c.toUpperCase()+"_blend"] = 0}
    // return obs
  }
  if(use_word_kcs){
  	obs[word.toLowerCase()] = abs_correct ? 1 : 0
  }

  // console.log(obs)
  return obs
}

export let update_knowlege_trace = (kt, kt_obs,params=default_params) => {
	for(let kc in kt_obs){
		if(!kc){continue}
		kt[kc] = bkt_update(kt[kc] || params.init, kt_obs[kc])
	}
	return kt
}





