function softmax(arr) {
    return arr.map(function(value,index) { 
      return Math.exp(value) / arr.map( function(y /*value*/){ return Math.exp(y) } ).reduce( function(a,b){ return a+b })
    })
}


let smol_ratio = (traces) =>{
	let log_traces = traces.map(Math.log)
	let min_log_trace = Math.min(...log_traces)
	let score = (log_traces.reduce((s, a) => s + a, 0.0)-min_log_trace*2)
	return score
}

let farthest_min = (traces) =>{
	let sorted_traces = [...traces]; sorted_traces.sort();
	let score = (sorted_traces[1] || 1.0) - (sorted_traces[0] || 1.0)
	return score
}

let lowest_min = (traces) =>{
	return 1.0 - Math.min(...traces)
}

let lowest_plus_farthest_min = (traces) => {
	return farthest_min(traces) + lowest_min(traces)
}

let score_words = (words, kt, prev_words =[], repeat_delay=2, score_func=lowest_min,epsilon=1e-10) => {
	let prev_n_words = (words.length > repeat_delay && prev_words.slice(-repeat_delay)) || []
	let scores = []
	let unrejected_words = []
	// console.log("prev_n_words", prev_n_words)
	for(let word of words){
		if(prev_n_words.includes(word)){continue}
		
		let word_info = words_info[word.toLowerCase()]
		let blend_kcs = (word_info['blends'] || []).map((x)=>x.toUpperCase()+'_blend')



		let kcs = blend_kcs.concat(word_info['graphemes'] || [])
		let traces = kcs.map((x)=>(kt[x] || default_params['init']))
		let score = score_func(traces)+epsilon
		// console.log("W", word, blend_kcs, score)
		unrejected_words.push(word)
		scores.push(score)
		
		// console.log("UR S", [...unrejected_words], [...scores])
	}
	return [unrejected_words, scores]
}



export let check_next_word_group_ind = (kt, group_ind=0) =>{
	// console.log("START check_expand_to_next_word_group")
	// let prev_ind = furthest_word_group_index
	
	// console.log("TT", group_ind, Object.keys(word_groups).length)
	while(group_ind < Object.keys(word_groups).length){
		let furthest_word_group = Object.keys(word_groups)[group_ind]
		let ag = group_allowed_graphemes[furthest_word_group]
		let graphemes = [...ag['consonants'], ...ag['vowels']]
		let blends = [...ag['blends']]
		//All graphemes must by > .6
		// console.log(graphemes.map((x)=>[x,(kt[x] || .2)]).filter((x)=>x[1] <= .6))
		let graphemes_okay = graphemes.every((x)=> (kt[x] || .2) > .6)

		//More than 50% of the blends must be > .6
		let blends_okay = true
		if(blends.length > 0){
			let blend_prop = blends.reduce((s,x)=> s+((kt[x.toUpperCase()+'_blend'] || .2) > .6),0)/blends.length
			blends_okay = blend_prop > .5	
			// console.log("BLEND", graphemes_okay, blend_prop)
		}
		// console.log(kt, graphemes_okay, blends_okay)
		
		if(graphemes_okay && blends_okay){
			group_ind += 1
			if(group_ind >= word_groups.length){
				break
			}
			furthest_word_group = Object.keys(word_groups)[group_ind]
			console.log("furthest_word_group", furthest_word_group)
		}else{
			break
		}
	}
	return group_ind

	// if(furthest_word_group_index > prev_ind){
	// 	words = [].concat(...Object.values(word_groups).slice(0,furthest_word_group_index+1))
	// 	console.log("NEW WORD LIST", [...words])
	// }
}

export let next_word = (words, kt, prev_words, repeat_delay=2, score_func=farthest_min) => {
	console.log(kt)
	let [ur_words, probs] = score_words(words, kt, prev_words, repeat_delay, score_func)	
	// console.log(ur_words, probs)
	// for(let i=0; i < probs.length; i++){
	// 	console.log(ur_words[i], probs[i])
	// }
	let chosen_word = PD.sample(ur_words,1,false,probs)[0]
	console.log("CHOSEN", chosen_word)
	return chosen_word
}



