import React, {Component} from 'react';
import { TouchableOpacity, TouchableWithoutFeedback, Easing, Animated, StyleSheet, Text, View, TextInput, Image, TouchableHighlight, PanResponder,Dimensions} from 'react-native';
import { star_images } from '../assets/misc';


class StarTotal extends Component {
  constructor(props){
    super(props);
    this.hop_anim = new Animated.Value(0)    
    this.scale_anim = new Animated.Value(1)    
    this.y_pos_anim = Animated.multiply(this.hop_anim, -this.props.jump_height) 
    this.scale_anim = Animated.add(1, Animated.multiply(this.hop_anim, this.props.grow_scale-1))

    this.state = {
    	n_stars : this.props.n_stars
    }
  }  

  animate_add_stars = async (from, to, interval=200, maxDuration=2000, min_interval=80) => {
  	this.setState({n_stars : from})

  	let total = Math.abs(to - from)
  	interval = Math.min(maxDuration/total, interval)
    let n_count = Math.ceil(min_interval/interval)
    let n_ticks = Math.floor(total/n_count)
    let mod_ticks = total % n_count 

    interval = maxDuration / (n_ticks + mod_ticks)
    console.log("<<", interval, n_ticks, mod_ticks)
  	for(let i=0; i<mod_ticks; i++){
  		await this.animate_add_star(interval)
  	}
    for(let i=0; i<n_ticks; i++){
      await this.animate_add_star(interval, n_count)
    }
  }

  animate_add_star = (interval=200,n_stars_added=1) => {

  	let config = {useNativeDriver : false}

  	// this.hop_anim.setValue(0)
  	let p = new Promise((resolve) => {
	    Animated.timing(this.hop_anim, {
	        toValue: 1,
	        duration : interval * .5,
	        easing : Easing.out(Easing.ease),
	        ...config
	    }).start(() => {
	    	this.setState({n_stars : this.state.n_stars + n_stars_added})

	    	Animated.timing(this.hop_anim, {
		        toValue: 0,
		        duration : interval * .5,
		        easing : Easing.in(Easing.ease),
		        ...config
	    	}).start(()=>{resolve()})
	    })
	})
	return p
  }

  render(){
      return (
    	<View style={styles.total_group}> 
            <Text selectable={false} style={styles.total_text}>{this.state.n_stars}</Text> 
            <Animated.Image 
            	source={star_images[this.props.kind + '_star']}
				      resizeMode='contain'
				      style={[styles.star, styles.total_image,{
				        transform : [{"translateY" : this.y_pos_anim}, {"scale": this.scale_anim}],
				      }]
				    }/>
        </View> 
        )
  }
}

StarTotal.defaultProps = {
  kind: "silver",
  jump_height: 40,
  grow_scale: 1.2,
  n_stars : 0,
}



