  clear_press_done_prompt() {
    if(this.press_done_prompt_timeout){
      clearTimeout(this.press_done_prompt_timeout)
    }
  }
  reset_press_done_prompt(repeats=2) {
    this.clear_press_done_prompt()

    // console.log("RESEEEET")

    this.press_done_prompt_timeout = setTimeout(async ()=>{
      if(!this.state.can_press_done || this.session_has_ended || this.state.exit_screen_displayed){
        this.clear_press_done_prompt()
        return
      }
      let y_anim = this.state.done_button_y_anim
      let scale = this.state.done_button_scale_anim
      let rotate = this.state.done_button_rotate_anim

      let config = {duration: 200,
        useNativeDriver: false}

      // console.log("HOPPPPPPPP")
      console.log("HOPPPPPPPP A")
      let anims = []
      for(let i=0; i < repeats;i++){
        anims.push(Animated.sequence([
          Animated.parallel([      
            Animated.timing(y_anim,{toValue: -20,
             easing: Easing.out(Easing.quad), ...config}),
            Animated.timing(scale,{toValue: 1.08,
             easing: Easing.out(Easing.quad), ...config}),
          ],{useNativeDriver:false}),
          Animated.parallel([
            Animated.timing(y_anim,{toValue: -30, ...config}),      
            Animated.sequence([
              Animated.timing(rotate,{toValue: -25,
               easing: Easing.inOut(Easing.quad), ...config}),
              Animated.timing(rotate,{toValue: 10,
               easing: Easing.inOut(Easing.quad), ...config}),
              Animated.timing(rotate,{toValue: 0,
               easing: Easing.inOut(Easing.quad), ...config}),
            ])
          ],{useNativeDriver:false}),
          Animated.parallel([      
            Animated.timing(y_anim,{toValue: 0,
             easing: Easing.in(Easing.quad), ...config}),
            Animated.timing(scale,{toValue: 1.00,
             easing: Easing.in(Easing.quad), ...config}),
          ],{useNativeDriver:false})
        ]))
      }
      console.log("HOPPPPPPPP B")
      handleAudio(["when_you.re_done_press_the_green_checkmark"])
      Animated.sequence(anims).start( 
        () => {this.reset_press_done_prompt()}
      )

    },PRESS_DONE_PROMPT_WAIT_TIME)

  }

  update_done(){
    Animated.spring(this.state.done_button_scale_anim,
      {toValue: (this.state.can_press_done && 1.0) || .95,
        ...default_anim_config
      }).start()
  }

  place_tile = (tile, index=-1) =>{
    if(tile.last_place_attempt == index){return}
    console.log("PLACE_ID", tile.id, index)
    this.has_interacted = true
    this.reset_press_done_prompt()
    let new_state = null
    let ut = [...this.state.unplaced_tiles]
    let pt = [...this.state.placed_tiles]
    
    if(pt.length < this.state.max_letters || tile.is_placed){
      if(!tile.is_placed){
        ut.splice(ut.indexOf(tile),1)
        pt.splice(index >= 0 ? index : pt.length ,0,tile)
        
        // this.refresh_placed()
        pt.forEach((x,i) => {x.needs_update = true; x.place_index = i})
        tile.needs_update = true
        tile.place_index = -1
        // ut.forEach((x,i) => {x.needs_update = true; x.place_index = -1})

        new_state = {"unplaced_tiles" : ut, "placed_tiles" : pt}

      }else{
        let old_index = pt.indexOf(tile)
        if(old_index != index){
          pt.splice(old_index,1)  
          pt.splice(index >= 0 ? index : pt.length ,0,tile)
          
          pt.forEach((x,i) => {x.needs_update = true; x.place_index = i})
          new_state = {"placed_tiles" : pt}
        }
      }
      tile.is_placed = true
      tile.above_thresh = false
    }else{
      console.log("FAILED PLACE", tile.last_place_attempt)
      // console.log()
      if(tile.last_place_attempt != null && !this.audio_lock){
        setTimeout(async ()=>{
          this.audio_lock = true
          await handleAudio("it_looks_like_you.ve_got_enough_letters")  
          await timeout(1000)
          this.audio_lock = false
        })
        
      }
      tile.last_place_attempt = index
    }
    
    if(new_state){
      let can_press_done = new_state['placed_tiles'].length > 0
      let should_resize_done = can_press_done != this.state.can_press_done

      this.setState({
       ...new_state,
       ...{"can_press_done" : can_press_done}
      })

      if(should_resize_done){this.update_done()}
    }
  }

  unplace_tile = (tile) =>{
    console.log("UNPLACE_ID", tile.id)
    let ut = this.state.unplaced_tiles
    let pt = this.state.placed_tiles
    let new_state = null

    this.reset_press_done_prompt()
    if(tile.is_placed){
      // console.log("UNPLACE!")
      pt.splice(pt.indexOf(tile),1)
      ut.splice(ut.length ,0,tile)

      pt.forEach((x,i) => {x.needs_update = true; x.place_index = i})
      ut.forEach((x,i) => {x.needs_update = true; x.place_index = -1})
      // this.refresh_placed()
      new_state = {
        "unplaced_tiles" : ut,
        "placed_tiles" : pt
      }
      tile.is_placed = false
      tile.above_thresh = true
      // tile.place_index = -1

    }

    tile.last_place_attempt = null
    if(new_state){
      let can_press_done = new_state['placed_tiles'].length > 0
      let should_resize_done = can_press_done != this.state.can_press_done

      this.setState({
        ...new_state,
        ...{"can_press_done" : can_press_done}
      })

      if(should_resize_done){this.update_done()}

      //, ()=>{this.updateTiles()})
      
    }
    

  }

