  async animate_correct_hopping(target='user_spelling',height=80,delay=120,){
    let tiles = target=='user_spelling' && this.state.placed_tiles ||
                this.state.correction_tiles;
    let promises = []
    for(let i=0; i < tiles.length; i++){
      let tf = tiles[i].tile_ref.current
      if(tf){
        let hop = tf.hop({delay:i*delay,to_color:'rgb(20,200,20)',height:height})
        promises.push(hop)
      }
    }
    return Promise.all(promises)
  }

