  check_spelling = async ()=>{
    //
    console.log("DONE!!!!")
    if(this.check_spelling_lock || !this.state.interaction_enabled){return}
    this.clear_press_done_prompt()

    

    await this.setState({"interaction_enabled":false, "can_press_done": false})
    this.update_done()

    // console.log("FALSE", this.state.interaction_enabled)
    
    this.has_interacted = true
    this.check_spelling_lock = true

    //wow great job
    //sound out spelling
    //lets see if we can make that sound more like ...
    //
    // console.log("D",this.state.placed_tiles.map((x)=>x.grapheme))
    let spelling = this.state.placed_tiles.map((x)=>x.grapheme.toUpperCase()).join("")
    let phonetic_correct, corr_seq;
    if(spelling == this.props.word){
      phonetic_correct = true
      corr_seq = []
    }else{
      corr_seq = best_correction_sequence(this.props.word,spelling,true)
      let c_obj = corr_seq[0]
      // console.log(c_obj)
      phonetic_correct = c_obj.is_correct
    }

    let abs_correct = spelling.toUpperCase() == this.props.word.toUpperCase()

    // Transaction Logging
    let transaction_data = {
      timestamp : Timestamp.now(), word : this.props.word, spelling : spelling,
      abs_correct:abs_correct, phonetic_correct:phonetic_correct, correction_seq: corr_seq.map(x=>x.toString())
    }

    // Star Stuff
    let [prev_total_silver, prev_total_gold, prev_earned_silver, prev_earned_gold] =  extract_prev_stars(this.props.user_data, this.props.word)

    let n_silver_possible = this.state.phonetic_spelling.length
    let n_earned_silver = !phonetic_correct ? Math.max(n_silver_possible-corr_seq.length,0) : n_silver_possible
    let word_stars = {"silver" : Math.max(n_earned_silver, prev_earned_silver), "gold" : Math.max(abs_correct, prev_earned_gold)}

    let additional_total_silver = word_stars.silver-prev_earned_silver
    let additional_total_gold = word_stars.gold-prev_earned_gold
    let new_total_silver = prev_total_silver + additional_total_silver
    let new_total_gold = prev_total_gold + additional_total_gold

    // BKT Stuff
    console.log(this.props.word, spelling)
    let kt_obs = find_knowlege_trace_observations(this.props.word, spelling, corr_seq)
    let kt = this.props.user_data.knowledge_trace || {}
    kt = update_knowlege_trace(kt, kt_obs)
    // let graphemes = this.get_word_graphemes(this.props.word)
    // let obs = {}
    // for(let gr of graphemes){
    //   console.log

    // }
    // graphemes.map()



    // console.log(spelling, is_correct, Math.max(n_silver_possible-corr_seq.length,0), n_silver_possible, n_earned_silver, corr_seq)
    // let prev_earned_stars = this.props.user_data.stars.words[]
    
    if(this.props.user_ref){
      console.log(this.props.user_ref)
      // updateDoc(this.props.user_ref, "transactions", arrayUnion(transaction_data))  
      updateDoc(
          this.props.user_ref, 
          "transactions", arrayUnion(transaction_data),
          new FieldPath("stars","words",this.props.word.toUpperCase()), word_stars,
          new FieldPath("stars","total_silver"), new_total_silver,
          new FieldPath("stars","total_gold"), new_total_gold,
          new FieldPath("knowledge_trace"), kt,
      )  
    }

    // this.props.user_data.stars.words[]

    if(!SKIP_FEEDBACK){
      await handleAudio([choice(['great_job','cool_spelling','nice_work'])])

      //Move away tiles
      for(let tile of this.state.all_tiles){tile.needs_update = true}
      this.updateTiles()

      if(this.props.sound_out_spelling){
        await handleAudio(['lets_sound_out_what_you_spelled'])
        await this.sound_out_spelling()
      }
      
      // if(phonetic_correct){
      if(abs_correct && this.props.verify_correct){
        await handleAudio(['this_spelling','is_the_correct_spelling_for',this.props.word])
        await this.animate_correct_hopping()
      }else if(phonetic_correct && this.props.verify_phonetically_correct){
        await handleAudio(['this_spelling','sounds_just_like',this.props.word])
        await this.animate_correct_hopping('user_spelling',0)
      }

      if(!abs_correct){
        if(this.props.incorrect_feedback_type == "incremental"){
          if(!phonetic_correct){
            await this.animate_incremental_feedback(spelling,corr_seq)
          }else{
            await this.animate_whole_word_feedback()
          }
        }else if(this.props.incorrect_feedback_type == "whole_word"){
          await this.animate_whole_word_feedback()
        }
      }
      
      // }else{
      //   
      // }    

      await timeout(400)
    }

    if(!SKIP_STARS){
      // Ensure stars are displayed
      await new Promise((resolve)=>{
        this.setState({"stars_displayed" : true}, ()=>{resolve()})  
      })
      
      let star_overlay = this.state.star_overlay_ref.current
      await star_overlay.display_stars()
      handleAudio(choice(['great_job','nice_work']))
      await star_overlay.earn_stars(n_earned_silver,abs_correct)
      
      await timeout(1200)
    }else{
      handleAudio(choice(['great_job','nice_work']))
      await timeout(400)
    }

    // Make local changes to stars
    let stars = this.props.user_data.stars || {words: {}, total_gold: 0, total_silver: 0}    
    stars.words[this.props.word.toUpperCase()] = word_stars
    stars.total_silver = new_total_silver
    stars.total_gold = new_total_gold

    this.check_spelling_lock = false
    this.props.onDone(kt, stars, phonetic_correct)
  }

