class StarOverlay extends Component {
  constructor(props){
    super(props);
    this.silver_star_anims = Array(props.num_silver_stars).fill().map(
          (_,i)=> ({
            fill : new Animated.Value(i < this.props.prev_earned_silver && 1 || 0),
            scale: new Animated.Value(0)
            }))

    this.gold_star_anim = {
      fill: new Animated.Value(this.props.prev_earned_gold && 1 || 0),
      scale: new Animated.Value(0)
    }

    this.state = {
      num_silver : this.props.num_silver,
      num_earned_silver : this.props.prev_earned_silver,
      has_earned_gold : this.props.prev_earned_gold,
      silver_total_ref : React.createRef(),
      gold_total_ref : React.createRef(),
    }

    // this.silver_total_ref = 
    // this.gold_total_ref = React.createRef()
   
    
  }

  componentDidMount = async () => {

    // this.props.is_ready_callback()()
    // // await timeout(1000)
    // await this.display_stars(this.state.num_silver,true)
    // this.earn_stars(3,true)

  }

  display_stars = async () => {
    // await timeout(2000)
    let p = new Promise((resolve) => {
      let config = {
         speed: 20,
          // velocity : 10,
          // bounciness : 8,
          // strength: 0.1,
          useNativeDriver: false,
      }
      let anims = []

      let n_silver = this.props.num_silver_stars
      for(let i=0; i <= n_silver; i++){
        let anim = i < n_silver && this.silver_star_anims[i] || this.gold_star_anim
        anim.scale.setValue(0)
        anims.push(Animated.spring(anim.scale, {
          toValue: .8,
          ...config
          })
        )
      }
      Animated.parallel(anims).start(async ()=>{
        await timeout(200)
        resolve()
      })
    })
    // await this.earn_star(-1,true)
    return p

  }

  tally_earned_stars = async (n_silver,n_gold) =>{
     this.state.silver_total_ref.current.animate_add_stars(0,n_silver)
     this.state.gold_total_ref.current.animate_add_stars(0,n_gold)
  }

  earn_stars = async (n_silver,earned_gold) => {
    // await timeout(2000)
    for(let i=0; i < n_silver; i++){
      await this.earn_star(i,false)
    }
    if(earned_gold){
      await this.earn_star(-1,true)  
    }
    

  }

  earn_star = (i,is_gold) => {
    let p = new Promise((resolve) => {
      if(i >= this.silver_star_anims.length){resolve()}
      let anim
      if(is_gold){
        anim = this.gold_star_anim
        // this.setState({"has_earned_gold" : true})
      }else{
        anim = this.silver_star_anims[i]
        // this.setState({"num_earned_silver" : Math.max(this.state.num_earned_silver, i)})
      }
      
      let config = {
          // duration : 200,
          // speed: 60,
          // velocity : 10,
          // bounciness : 8,
          // strength: 0.1,
          useNativeDriver: false,
      }

      Animated.timing(anim.fill, {
        toValue: 1,
        duration : 500,
        easing : Easing.out(Easing.ease),
        ...config
      }).start()


      Animated.timing(anim.scale, {
        toValue: .5,
        duration : 250,
        easing : Easing.in(Easing.ease),
        ...config
      }).start(() => {        

        resolve()
        let is_new_star = false
        if(is_gold){
          if(is_gold > this.props.prev_earned_gold){
            is_new_star = true
            this.state.gold_total_ref.current.animate_add_star(270)  
          }
        }else{
          if(i >= this.props.prev_earned_silver){
            is_new_star = true
            this.state.silver_total_ref.current.animate_add_star(270)
          }
        }

        Animated.timing(anim.scale, {
        toValue: is_new_star && 1.5 || 1,
        duration : 250,
        easing : Easing.out(Easing.ease),
        ...config
        }).start(() => {
          if(is_new_star){
            Animated.timing(anim.scale, {
            toValue: 1.25,//is_new_star && 1.2 || 1,
            duration : 200,
            easing : Easing.out(Easing.ease),
            ...config
            }).start()  
          }
        })
      })

    })
    return p
  }

  render(){
      let stars = []
      for(let i=0; i < this.props.num_silver_stars; i++){
        // let ss = (i < this.state.num_earned_silver && "silver_star") || "empty_silver_star"
        let fill_anim = this.silver_star_anims[i].fill
        let inv_fill_anim = Animated.subtract(1,fill_anim)
        let scale_anim = this.silver_star_anims[i].scale
        stars.push(
          <View key = {"silver" + i.toString()} style={[styles.star_container]}>
            <Animated.Image source={star_images['silver_star']}
                    resizeMode='contain'
                    // key = {"silver" + i.toString()}
                    style={[styles.star,{
                          transform : [{"scale" : scale_anim}],
                          opacity : fill_anim}]
                        }
            />
            <Animated.Image source={star_images['empty_silver_star']}
                    resizeMode='contain'
                    // key = {"silver_empty" + i.toString()}
                    style={[styles.star,{
                          transform : [{"scale" : scale_anim}],
                          opacity : inv_fill_anim}]
                        }
            />
          </View>

        )
      }

      // let ss = (this.state.has_earned_gold && "gold_star") || "empty_gold_star"
      let fill_anim = this.gold_star_anim.fill
      let inv_fill_anim = Animated.subtract(1,fill_anim)
      let scale_anim = this.gold_star_anim.scale
      stars.push(
        <View key = "gold" style={[styles.star_container]}>
            <Animated.Image source={star_images['gold_star']}
                    // key = "gold"
                    resizeMode='contain'
                    style={[styles.star,{
                          transform : [{"scale" : scale_anim}],
                          opacity : fill_anim}]
                        }
            />
            <Animated.Image source={star_images['empty_gold_star']}
                    resizeMode='contain'
                    // key = "gold_empty"
                    style={[styles.star,{
                          transform : [{"scale" : scale_anim}],
                          opacity : inv_fill_anim}]
                        }
            />
          </View>
      )
      // stars = []
      return (
        <View style={styles.star_overlay}>
          {!this.props.is_end_of_session && 
          <View style={styles.star_row}>
            {stars}
          </View>
          }
          <View style={styles.star_row}>
            <StarTotal key={'total_silver'} kind={'silver'} n_stars={this.props.prev_total_silver} ref={this.state.silver_total_ref}/>
            <StarTotal key={'total_gold'} kind={'gold'} n_stars={this.props.prev_total_gold} ref={this.state.gold_total_ref}/>
            {this.props.is_end_of_session && 
              <View style={styles.star_row}>
                <TouchableHighlight 
                  style={styles.return_button}
                  onPress={(() => {this.props.onEndSession()})}>
                  <Text style={styles.return_button_text}>{""}</Text>
                </TouchableHighlight>
              </View>
            }
            {/*<View style={styles.total_group}> 
            <Text style={styles.total_text}>{"67"}</Text> 
            <Animated.Image source={star_images['silver_star']}
                      resizeMode='contain'
                      key = "silver_total"
                      style={[styles.star, styles.total_image,{
                            transform : [{"translateY" : -scale_anim*50}],
                            }]
                          }
              />
            </View> 

            <View style={styles.total_group}>  
              <Text style={styles.total_text}>{"67"}</Text> 
              <Animated.Image source={star_images['gold_star']}
                      resizeMode='contain'
                      key = "gold_total"
                      style={[styles.star, styles.total_image,{
                            transform : [{"translateY" : -scale_anim*50}],
                            }]
                          }
              />
            </View> */}


          </View>
        </View>
        )
  }
}

